<template>
  <div class="home pa-6">
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="$store.state.search"
      :items-per-page=-1
      @click:row="handleClick"
    >
      <template v-slot:item.image="{item}">
        <v-img :src="item.image" width="198px" height="40px" />
      </template>

    </v-data-table>
      
    <v-overlay
      :value="overlay"
    >
      <v-img :src="currentDetail"/>
      <v-btn color="success" @click="overlay = false" >
        关闭
      </v-btn>
    </v-overlay>
  </div>
</template>

<script>
import json from './data.json'
var tabledata = []
for (var i_map = 0; i_map < json.maps.length; i_map++)
{
  var map = json.maps[i_map];
  for (var i = 0; i < map.shops.length; i++)
  {
    var shop = map.shops[i];
    for (var j = 0; j < shop.goods.length; j++)
    {
      var good = shop.goods[j];
      tabledata.push({
        image: map.map+"/"+good.index+".jpg",
        name: good.name,
        price: good.price,
        shop: shop.title,
        map: map.map,
        avail: good.available,
        time: map.time,
        detail: map.map+"/"+good.index+"_detail.jpg",
      });
    }
  }
}
  export default {
    data() {
      return {
        overlay: false,
        currentDetail: '',
        name: 'Home',
        headers: [
          { text: '截图', value: 'image' },
          { text: '名字', value: 'name' },
          { text: '价格', value: 'price' },
          { text: '商店', value: 'shop' },
          { text: '位置', value: 'map' },
          { text: '有货', value: 'avail' },
          { text: '时间', value: 'time' },
        ],
        desserts: tabledata,
      }
    },
    methods: {
      handleClick(value) {
        console.log(value.detail)
        this.currentDetail = value.detail
        this.overlay = !this.overlay
      },
    }
  }
</script>
